export default {
	props: {
		orderData: {
			type: Array,
			default: function() {
				return []
			}
		},
		defaultSeatId: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			dSeatId:0,
			whentherSeat:false
		}
	},
	methods: {
		seatSelect: function(num, id) {
			this.whentherSeat=true
			if (num > 0) {
				this.dSeatId = id;
				this.defaultSeatId = id;
			}
		}
	}
}
